//benefits styles
.benefits {
  padding-bottom: 82px;
  padding-top: 97px;
  border-bottom: 1px solid #e6e7eb;

  @include media("<tab") {
    padding-bottom: 40px;
    padding-top: 48px;
  }

  &__list {
    display: flex;
    flex: 0 1 auto;
    flex-wrap: wrap;
    margin-right: -56px;

    @include media("<xsmall") {
      margin-right: 0px;
    }
  }

  &__item {
    margin-right: 56px;
    width: calc((100% / 4) - 56.1px);
    text-align: center;
    margin-bottom: 30px;

    @include media("<tab") {
      width: calc((100% / 2) - 56.1px);
    }

    @include media("<xsmall") {
      width: 100%;
      margin-right: 0;
    }

    &:hover {
      .benefits__icon {
        &:after {
          transform: rotate(225deg);
          @include transition-1;
        }
      }

      .benefits__title {
        color: $red;
        @include transition-1;
      }

      .benefits__icon {
        &:after {
          border: 1px $red solid;
          @include transition-1;
        }
      }
    }
  }

  &__icon {
    width: 73px;
    height: 73px;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 auto 24px auto;
    position: relative;
    background-size: auto auto;

    &:after {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 2px;
      border: 1px rgba(153, 153, 153, 0.25) solid;
      @include transition-1;
    }
  }

  &__title {
    color: $dark;
    font-family: $second-font-family;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1.35px;
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 17px;
    @include transition-1;

    &:after {
      position: absolute;
      content: "";
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      width: 42px;
      height: 1px;
      background-color: $dark;
    }
  }

  &__text {
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.9px;
  }
}
