//clients styles
.clients {
  padding: 150px 0 110px 0;
  background-color: $light;
  border-bottom: 1px solid #d8d9db;
  @include media("<tab") {
    padding: 75px 0 55px 0;
  }

  &__inner {
    display: flex;
    flex: 0 1 auto;
    flex-wrap: wrap;
    margin-right: -30px;
  }

  &__item {
    width: calc(100% / 5 - 30.1px);
    margin-right: 30px;
    margin-bottom: 30px;
    height: 100px;
    box-shadow: 0 1px 3px rgba(31, 31, 31, 0.09);
    background-color: #ffffff;
    @include transition-1;

    &:hover {
      box-shadow: 0 3px 9px 1px rgba(31, 31, 31, 0.3);
      @include transition-1;

      .clients-list-item__image {
        opacity: 1;
      }
    }

    @include media("<tab") {
      width: calc(100% / 3 - 30.1px);
    }

    @include media("<xsmall") {
      width: calc(100% / 2 - 30.1px);
    }
  }

  &__image {
    height: 100%;
    opacity: 0.6;
    display: block;
    box-sizing: border-box;
    padding: 10px;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
