//stats styles
.stats {
  min-height: 511px;
  background-size: cover;
  background-color: #fff;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;

  &:after {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
  }

  &__inner {
    display: flex;
    flex: 0 1 auto;
    flex-wrap: wrap;
    padding: 143px 0;
    align-items: center;
    min-height: 511px;
    position: relative;
    z-index: 1;
    box-sizing: border-box;
    justify-content: space-between;

    @include media("<tab") {
      padding: 70px 0;
    }
  }

  &__item {
    width: 20%;
    flex: 1 1 20%;

    @include media("<tab") {
      width: 50%;
      flex: 1 1 50%;
      margin-bottom: 30px;
    }

    @include media("<xsmall") {
      width: 100%;
      flex: 1 1 100%;
      margin-bottom: 30px;
    }
  }
  &__content {
    color: #fff;
    text-align: center;
  }

  &__icon {
    width: 111px;
    height: 111px;
    border: 1px #fff solid;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 100px;
    margin: 0 auto 50px auto;
  }

  &__count {
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 1.5px;
  }
}
